import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  SET_STATUS_JAWAB_SOAL,
  SET_HASIL_JAWAB_SOAL,
  SET_JAWABAN_SOAL_BENAR_SALAH,
} from "../../../redux/actions";
import { CInputRadio, CLabel, CFormGroup } from "@coreui/react";
import endpoints from "../../../utils/endpoints";
import request from "../../../utils/request";

function PilihanGanda(props: Record<string, any>) {
  const { dataSoal: DATA_SOAL } = props;
  const dataSoal = useSelector((state) => state.dataSoal);
  const dispatch = useDispatch();
  const mapNumToChar = (index) => {
    const abjad = "ABCDEFGHIJKLMNOPQRSTUPWZYZ";
    return abjad.split("")[index];
  };
  const strip_html_tags = (str) => {
    if (str === null || str === "") return false;
    else str = str.toString();
    return str.replace(/<[^>]*>/g, "");
  };
  const generateHTML = (html, isOption = false) => {
    const includeImage = html.split("data:image");
    html = html.replace(
      '<p data-f-id="pbf" style="text-align: center; font-size: 14px; margin-top: 30px; opacity: 0.65; font-family: sans-serif;">Powered by <a href="https://www.froala.com/wysiwyg-editor?pb=1" title="Froala Editor">Froala Editor</a></p>',
      ""
    );
    if (!isOption) return html;
    if (includeImage.length > 1) {
      return html;
    } else {
      return strip_html_tags(html);
    }
  };

  useEffect(() => {
    if (dataSoal.status[dataSoal.current.index].status === "dijawab") {
      const data = {
        exam_user_question_id: dataSoal.soal[dataSoal.current.index].id,
        jawaban: dataSoal.soal[
          dataSoal.current.index
        ].exam_question_answer.jawaban.map((item) => {
          return {
            status_jawaban: item.status_jawaban,
          };
        }),
      };
      const config = {
        ...endpoints.jawabSoalPilgan,
        data,
      };
      request(null, config)
        .then((res) => {
          dispatch({
            type: SET_HASIL_JAWAB_SOAL,
            payload: { hasil: res.data.status_jawaban },
          });
        })
        .catch((err) => console.log(err));
    }
    console.log("data pilgan vertical ->>", DATA_SOAL);
  }, [DATA_SOAL]);

  return (
    <div className="row">
      <div className="col-12">
        {DATA_SOAL.exam_question_articles.map((item, index) => {
          return (
            <div className="mb-2" key={index}>
              <p className="text-center font-weight-bold">{item.judul}</p>
              <div
                dangerouslySetInnerHTML={{ __html: generateHTML(item.artikel) }}
              />
            </div>
          );
        })}
        <p
          className="text-primary mb-1"
          dangerouslySetInnerHTML={{
            __html: generateHTML(DATA_SOAL.exam_question_answer.pertanyaan),
          }}
        />
        <table className="table-bordered">
          <thead>
            <tr>
              <th>Pertanyaan</th>
              {DATA_SOAL.exam_question_answer.pola_jawaban.map(
                (item, index) => {
                  return <th key={index}>{item.label}</th>;
                }
              )}
            </tr>
          </thead>
          <tbody>
            {DATA_SOAL.exam_question_answer.jawaban.map((item, index) => {
              return (
                <tr key={index}>
                  <td>
                    <span
                      dangerouslySetInnerHTML={{
                        __html: generateHTML(item.text_jawaban),
                      }}
                    />
                  </td>
                  <td className="text-center">
                    <input
                      type="radio"
                      name={`benar-salah-${index}`}
                      checked={item.status_jawaban === "1"}
                      onChange={() => {
                        dispatch({
                          type: SET_JAWABAN_SOAL_BENAR_SALAH,
                          payload: { index, status: "1" },
                        });
                        dispatch({
                          type: SET_STATUS_JAWAB_SOAL,
                          payload: { status: "dijawab" },
                        });
                      }}
                    />
                  </td>
                  <td className="text-center">
                    <input
                      type="radio"
                      name={`benar-salah-${index}`}
                      checked={item.status_jawaban === "0"}
                      onChange={() => {
                        console.log(1);
                        dispatch({
                          type: SET_JAWABAN_SOAL_BENAR_SALAH,
                          payload: { index, status: "0" },
                        });
                        dispatch({
                          type: SET_STATUS_JAWAB_SOAL,
                          payload: { status: "dijawab" },
                        });
                      }}
                    />
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default PilihanGanda;
