import React, { useState, useEffect } from "react";
import {
  CContainer,
  CButton,
  CModal,
  CModalHeader,
  CModalFooter,
  CModalTitle,
  CModalBody,
} from "@coreui/react";
import { useSelector, useDispatch } from "react-redux";
import { Link, useLocation, useHistory } from "react-router-dom";
import { RESET_USER } from "../../../redux/actions";
import endpoints from "src/utils/endpoints";
import request from "src/utils/request";
import BGHeader from "../../BackgroundHeader";
import logo from "../../../assets/images/logo-rasyiidu-black.png";

function DataConfirmation({ children }) {
  const history = useHistory();
  const dispatch = useDispatch();
  const [confirmLogout, setConfirmLogout] = useState(false);
  const { profile } = useSelector((state) => state.user);
  const logout = async () => {
    try {
      // const response = await request(null, endpoints.logout);
      dispatch({ type: RESET_USER, payload: null });
      history.push("/login");
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <main className="c-main">
      <CModal show={confirmLogout} shape="rounded">
        <CModalHeader>
          <CModalTitle>Info</CModalTitle>
        </CModalHeader>
        <CModalBody>Apakah anda yakin akan keluar ?</CModalBody>
        <CModalFooter>
          <CButton color="danger" onClick={() => logout()}>
            Ya
          </CButton>
          <CButton color="success" onClick={() => setConfirmLogout(false)}>
            Tidak
          </CButton>
        </CModalFooter>
      </CModal>
      <BGHeader />
      <CContainer fluid>
        <div className="d-flex justify-content-between">
          <img src={logo} alt="logo-rasyiidu" className="center" />
          <div className="">
            <strong className="text-white float-right mr-1">
              {profile.nama}
            </strong>
            <br />
            <CButton
              color="light"
              className="float-right font-weight-bold"
              shape="pill"
              onClick={() => setConfirmLogout(true)}
            >
              Logout
            </CButton>
          </div>
        </div>
        {children}
      </CContainer>
    </main>
  );
}

export default DataConfirmation;
